: border-box;\n    font-family: \"TT Lakes\", serif;\n}\n\nbody {\n    min-width: 320px;\n    max-width: 1440px;\n    margin: 0 auto;\n    overflow-x: hidden;\n    background-color: $background-grey;\n}\n\n@import \"modules/header\";\n@import \"modules/main-header\";\n@import \"modules/main-info\";\n@import \"modules/main-brands\";\n@import \"modules/main-services\";\n@import \"modules/main-prices\";\n@import \"modules/main-application\";\n@import \"modules/footer\";\n","$main-text-color: #1b1c21;\n$pink: #ff3e79;\n$light-grey: #eaeaea;\n$grey: #7e7e82;\n$green: #41f6d7;\n$background-grey: #f8f8f8;\n$white: #ffffff;\n$green-focus: #b8ffec;\n$swiper-bullet: #dddddd;\n$swiper-bullet-active: #b5b6bc;",".logo__img {\n  vertical-align: middle;\n}\n\n.hidden {\n  display: none;\n}\n\n.wrapper {\n  display: flex;\n  justify-content: space-between;\n}\n\n.header {\n  padding: 0 16px;\n  position: fixed;\n  background-color: white;\n  height: 88px;\n  width: 100%;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  z-index: 10;\n\n  &__left {\n    display: flex;\n    justify-content: flex-start;\n    align-items: center;\n    gap: 1rem;\n  }\n\n  &__label {\n    gap: 8px;\n    display: flex;\n    align-items: center;\n  }\n\n  &__text-hidden {\n    display: none;\n    color: $main-text-color;\n    text-align: right;\n    font-size: 16px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: 24px;\n    letter-spacing: -0.2px;\n  }\n\n  &__buttons {\n    display: flex;\n    align-items: center;\n    gap: 10px;\n  }\n\n}\n\n.button {\n  display: block;\n  border: none;\n\n  &__circle {\n    width: 40px;\n    height: 40px;\n    border-radius: 50%;\n    background-color: $pink;\n  }\n\n  &__menu {\n    background-image: url(\"../../img/menu.svg\");\n  }\n\n  &__call {\n    display: none;\n    background-image: url(\"../../img/call.svg\");\n  }\n\n  &__chat {\n    display: none;\n    background-image: url(\"../../img/chat.svg\");\n  }\n\n  &